.scrollup {
    position: fixed;
    right: 2.5rem;
    bottom: -20%;
    background-color: rgba(0, 0, 0, .8);
    opacity: 0.8;
    padding: 0.3rem 0.5rem;
    border-radius: 0.4rem;
    z-index: var(--z-tooltip);
    transition: .4s;
}

.scrollup__icon {
    font-size: 1.5rem;
    color: var(--container-color);
}

.scrollup:hover {
    background-color: var(--title-color-dark);
}

/* show scroll */
.show-scroll {
    bottom: 3rem;
}

  /*BREAKPOINTS*/
/*large devices*/
@media  screen and (max-width: 992px) {
 .scrollup {
    right: 1.5rem;
    padding: 0.25rrem 0.4rem;
 }

 .scrollup__icon {
    font-size: 1.25rem;
 }
  }
  
  /*medium devices*/
  @media  screen and (max-width: 768px) {
    .show-scroll {
        bottom: 4rem;
    }
  }
  
  /*small devices*/
  @media  screen and (max-width: 350px) {
  .show-scroll {
    right: 1rem;
  }
  }
    